.card-photo {
  background-color: white;
  position: relative;
  height: 20rem;
  width: 20rem;
  overflow: hidden;
  float: left;
  margin: 15px;
}

.card-photo .content {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #202020;
  top: calc(90% - 80px);
  left: -2.04rem;
  opacity: 1;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.card-photo .content .content-inner {
  position: relative;
  height: 80px;
  display: flex;
  flex-flow: column nowrap;
  text-align: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
}

.card-photo .content:after {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 30px;
  height: 30px;
  border-color: #EEEEEE;
  border-style: solid;
  border-width: 2px 0 0 2px;
  content: "";
}

.card-photo .title {
  font-weight: 600;
  text-transform: uppercase;
  color: #EEEEEE;
  letter-spacing: 1px;
}

.card-photo .description {
  color: #EEEEEE;
}

.card-photo:after {
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  border-color: #EEEEEE;
  border-style: solid;
  border-width: 0 2px 2px 0;
  content: "";
}

.card-photo:hover {
  cursor: pointer;
}

.card-photo:hover .content {
  top: -2.3rem;
  background: #202020;
  transition: all 0.5s ease-in-out;
  left: -2rem;
}

.card-photo:hover .content .content-inner {
  height: 100%;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease-in-out;
}
.project{
  margin-top: 5rem;
