.scroll-downs {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  padding-bottom: -12rem;

  width: 34px;
  height: 55px;
}
.mousey {
  width: 3px;
  padding: 10px 15px;
  height: 35px;
  border: 3px solid yellow;
  border-radius: 25px;
  opacity: 0.75;
  box-sizing: content-box;
}
.scroller {
  width: 3px;
  height: 10px;
  border-radius: 25%;
  background-color: #ff4c00;
  animation-name: scroll;
  animation-duration: 2.2s;
  animation-timing-function: cubic-bezier(0.15, 0.41, 0.69, 0.94);
  animation-iteration-count: infinite;
}
@keyframes scroll {
  0% {
    opacity: 0;
  }
  10% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(15px);
    opacity: 0;
  }
}

.card-photo {
  background-color: rgb(161, 63, 63);
  position: relative;
  height: 20rem;
  width: 20rem;
  overflow: hidden;
  float: left;
  margin: 15px;
}

.card-photo .content {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #202020;
  top: calc(90% - 80px);
  left: -2.04rem;
  opacity: 1;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.card-photo .content .content-inner {
  position: relative;
  height: 80px;
  display: flex;
  flex-flow: column nowrap;
  text-align: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
}

.card-photo .content:after {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 30px;
  height: 30px;
  border-color: #eeeeee;
  border-style: solid;
  border-width: 2px 0 0 2px;
  content: "";
}

.card-photo .title {
  font-weight: 600;
  text-transform: uppercase;
  color: #eeeeee;
  letter-spacing: 1px;
}

.card-photo .description {
  color: #eeeeee;
}

.card-photo:after {
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  border-color: #eeeeee;
  border-style: solid;
  border-width: 0 2px 2px 0;
  content: "";
}

.card-photo:hover {
  cursor: pointer;
}

.card-photo:hover .content {
  top: -2.3rem;
  background: #202020;
  transition: all 0.5s ease-in-out;
  left: -2rem;
}

.card-photo:hover .content .content-inner {
  height: 100%;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease-in-out;
}
