#root{
  background-color: black;
  width: 100%;
  height: 100%;
}
.glowing-button {
  transition: box-shadow 0.3s ease-in-out; 
}

.glowing-button:hover {
  animation: glowing 1.5s infinite;
}

@keyframes glowing {
  0% {
    box-shadow: 0 0 5px #FF4500;
  }
  50% {
    box-shadow: 0 0 20px #FF4500;
  }
  100% {
    box-shadow: 0 0 5px #FF4500;
  }
}
